<template>
  <div class="my-main-container">
    <div class="importWrap">
      <el-button type="primary" @click="openTheImport"></el-button>
      <el-button
        type="primary"
        @click="toExport(tableData, tableHeader)"
        style="margin-left: 10px"
      >
        
      </el-button>
    </div>
    <el-table
      :data="tableData"
      border
      row-class-name="rowTableStyle"
      cell-class-name="cellTableStyle"
      header-row-class-name="rowHeaderTableStyle"
      header-cell-class-name="cellHeaderTableStyle"
      :cell-style="tableCellStyle"
      :header-cell-style="tableHeaderCellStyle"
    >
      <el-table-column type="index" label="" />
      <el-table-column
        :prop="item"
        :label="item"
        v-for="(item, index) in tableHeader"
        :key="index"
      />
    </el-table>
  </div>

  <!--  -->
  <el-dialog
    v-model="dialogVisible"
    title=""
    width="570px"
    :before-close="handleClose"
  >
    <div class="uploadFileWrap">
      <el-upload
        class="upload-demo"
        ref="uploadFile"
        drag
        action="#"
        accept=".xls,.xlsx"
        :auto-upload="false"
        :multiple="false"
        :on-change="onFileChange"
      >
        <el-icon class="el-icon--upload"><upload-filled /></el-icon>
        <div class="el-upload__text">
          <em></em>
        </div>
        <template #tip>
          <div class="el-upload__tip">.xls,.xlsx500kb</div>
        </template>
      </el-upload>
    </div>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="beTrueImport"> </el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import * as XLSX from "xlsx";
import ExportJsonExcel from "js-export-excel";
import { UploadFilled } from "@element-plus/icons";

export default {
  name: "importExcel",
  data() {
    return {
      tableHeader: ["date", "name", "address"],
      tableData: [
        {
          date: "2016-05-03",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
        },
        {
          date: "2016-05-02",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
        },
        {
          date: "2016-05-04",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
        },
        {
          date: "2016-05-01",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
        },
      ],
      dialogVisible: false,
    };
  },
  components: { UploadFilled },
  created() {},
  mounted() {},
  methods: {
    beTrueImport() {
      const _self = this;
      const uploadFile = this.$refs.uploadFile.uploadFiles;
      // 
      // const { raw: files } = uploadFile[0];
      const files = uploadFile[0].raw;
      // FileReader
      const fileReader = new FileReader();
      fileReader.onload = (event) => {
        try {
          const { result } = event.target;
          // excel
          const workbook = XLSX.read(result, { type: "binary" });
          console.log("workbook", workbook);
          let data = []; // 
          // 
          for (const sheet in workbook.Sheets) {
            if (workbook.Sheets.hasOwnProperty(sheet)) {
              //  sheet_to_json  excel  json 
              data = data.concat(
                XLSX.utils.sheet_to_json(workbook.Sheets[sheet], { defval: "" })
              );
              break; // 
            }
          }
          _self.setTableArray(data);
        } catch (e) {
          // 
          console.log("");
          return;
        }
      };
      // 
      fileReader.readAsBinaryString(files);
    },
    setTableArray(val) {
      this.tableData = [...val];
      this.tableHeader = Object.keys(val[0]);
      this.dialogVisible = false;
    },
    openTheImport() {
      this.dialogVisible = true;
    },
    handleClose(done) {
      done();
    },
    toExport(data, headData) {
      const option = {
        fileName: "excel",
        datas: [
          { sheetData: data, sheetFilter: headData, sheetHeader: headData },
        ],
      };
      const toExcel = new ExportJsonExcel(option); //new
      toExcel.saveExcel(); //
    },
    onFileChange(file) {
      this.$refs.uploadFile.uploadFiles = [];
      this.$refs.uploadFile.uploadFiles.push(file);
    },
    //  table cell
    tableCellStyle() {
      // return "border-color: #868686;";
    },
    //  table header cell
    tableHeaderCellStyle() {
      // return "border-color: #868686; color: #606266;";
    },
  },
};
</script>
